import { Row, Col, Tooltip } from "antd";
import { withTranslation, TFunction } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { Button } from "../../common/Button";
import { MiddleBlockSection, Content, ContentWrapper, Socials } from "./styles";
import { SvgIcon } from "../../common/SvgIcon";

interface MiddleBlockProps {
  title: string;
  content: string;
  button?: string;
  t: TFunction;
  id: string;
}
interface SocialLinkProps {
  href: string;
  src: string;
}
const MiddleBlock = ({ title, content, button, t , id}: MiddleBlockProps) => {
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
  };

  const SocialLink = ({ href, src }: SocialLinkProps) => {
    return (
      <Tooltip title={src.split('.')[0]}>
        <a
          href={href}
          target="_blank"
          rel="noopener noreferrer"
          key={src}
          aria-label={src}
        >
          <SvgIcon src={src} width="25px" height="25px" />
        </a>
      </Tooltip>
    );
  };

  return (
    <MiddleBlockSection id={id}>
      <Slide direction="up" triggerOnce>
        <Row justify="center" align="middle">
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <h6>{t(title)}</h6>
              <Content>{t(content)}</Content>
              {button && (
                <Button name="submit" onClick={() => scrollTo(id)}>
                  {t(button)}
                </Button>
              )}
              <Socials>
                <SocialLink
                  href="https://github.com/shivalagisetty/"
                  src="github.svg"
                />
                <SocialLink
                  href="https://www.linkedin.com/in/lss311/"
                  src="linkedin.svg"
                />
                <SocialLink
                  href="https://medium.com/@sampathshiva311"
                  src="medium.svg"
                />
              </Socials>
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </MiddleBlockSection>
  );
};

export default withTranslation()(MiddleBlock);
